.title {
  color: $blue-color;
  font-family: $title-font;
  font-size: 40px;
  line-height: 49px;
  text-transform: uppercase;
  @media screen and (max-width: 767px) {
    font-size: 32px;
    line-height: 39px;
  }
}
.button {
  background: $gradient-color;
  border-radius: 80px;
  border: none;
  color: $white;
  cursor: pointer;
  font-family: $medium-font;
  font-size: 17px;
  line-height: 20px;
  padding: 20px 40px;
  text-transform: uppercase;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
  transition: all linear 0.5s;
  &:hover {
    background: linear-gradient(143.55deg, #ffc341 -50.3%, #ffd701 63.1%);
  }
  &:focus {
    outline: none;
  }
  &:active {
    background: radial-gradient(150% 119.49% at 50% 50.85%, rgba(0, 0, 0, 0) 30.67%, rgba(3, 3, 3, 0.24) 100%), linear-gradient(143.55deg, #ffc341 -50.3%, #ffd701 63.1%);
    outline: none;
  }
}
.input {
  border: 2px solid $border-color;
  border-radius: 80px;
  color: $base-text-color;
  font-family: $medium-font;
  font-size: 17px;
  line-height: 20px;
  outline: none;
  position: relative;
  text-transform: uppercase;
  &::placeholder {
    opacity: 0.5;
  }
  &:hover {
    border-color: #ffc341;
  }
  &:focus {
    border-color: #ffc341;
  }
}

label {
  position: relative;
}
input.input__error {
  border-color: #ff0000;
}
input.error-message {
  bottom: -20px;
  color: rgba(#ff0000, 0.5);
  font-size: 13px;
  left: 35px;
  position: absolute;
}

.social {
  &__item {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }
  &__link {
    cursor: pointer;
    &:hover {
      svg {
        opacity: 0.6;
      }
    }
  }
  &__icon {
    fill: black;
    opacity: 0.2;
    transition: opacity linear 0.15s;
  }
}

.card {
  display: flex;
  margin-bottom: 32px;
  padding-right: 75px;
  @media screen and (max-width: 1023px) {
    align-items: center;
    flex-direction: column;
    padding-right: 0;
  }
  @media screen and (max-width: 767px) {
    align-items: flex-start;
    margin-bottom: 39px;
  }
  &__picture {
    margin-right: 20px;
    @media screen and (max-width: 1023px) {
      margin-bottom: 20px;
      margin-right: 0;
    }
    @media screen and (max-width: 767px) {
      margin-bottom: 16px;
    }
  }
  &__image {
    @media screen and (max-width: 1023px) {
      height: auto;
      width: 224px;
    }
    @media screen and (max-width: 767px) {
      height: auto;
      width: 112px;
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
  }
  &__curator {
    display: flex;
    line-height: 22px;
    margin-bottom: 12px;
    @media screen and (max-width: 767px) {
      font-size: 15px;
      line-height: 22px;
    }
    &-title {
      color: $blue-color;
      margin-right: 8px;
    }
    &-name {
      opacity: 0.74;
    }
  }
  &__address {
    font-size: 13px;
    line-height: 19px;
    margin-bottom: 10px;
    opacity: 0.74;
    @media screen and (max-width: 767px) {
      margin-bottom: 12px;
    }
  }
  &__social {
    display: flex;
    margin-top: auto;
  }
  &__link {
    &:not(:last-child) {
      margin-right: 12px;
    }
  }
}
